@use '/src/assets/css/variables' as *;

.page_catalog {

    & .container {

    
        & > h2 {
            text-align: center;
            margin-top: 140px;
            color: var(--main_vv);
            margin-left: 150px;

            @include respond-to(sm) {
                margin-top: 90px;
                margin-left: 0;
            }
        }
    
        .splider_catalog__main {
            height: 750px;
            margin-top: 150px;
            margin-bottom: 50px;
            display: flex;
            align-items: stretch;
            width: 100%;
            position: relative;
        
       
            @include respond-to(md) {
                height: 650px;
            }
        
            @include respond-to(sm) {
                margin-top: 40px;
                height: auto;
                flex-direction: column;
            }
    
    
        }
    
    
    
    
    }


}
    